import uniIcons from '@/components/uni-icons/uni-icons.vue'
import glanceSlideNavTabBar from '@/wxcomponents/glance-SlideNavTabBar/glance-SlideNavTabBar.vue'
export default {
	components: {uniIcons, glanceSlideNavTabBar},
	data() {
		return {
			imgUrl: "",
			longitude: "", //经度
			latitude: "",
			locationArea: "", //定位区域  广州市-海珠区
			city: "",
			area: "",
			ban1List: [],
			ban2List: [],
			businessList: [], //商家分类
			topList: [], //顶部 四个入口
			initForm: {
				cate_id: "",
				lng: "",
				lat: ""
			}, //
			dian: [], //分类下的店
			
		}
	},
	onLoad() {
		this.imgUrl = this.$store.state.imgUrl;
		uni.setStorageSync('pageCur', 0);
		this.getLocation();
		this.getTopClass();
		this.getFirstBanner();
		this.getSecondBanner();
	},
	methods: {
		getLocation() {
			uni.getLocation({
			    type: 'wgs84',
			    success:  (res)=> {
			        console.log('当前位置的经度：' + res.longitude);
			        console.log('当前位置的纬度：' + res.latitude);
					uni.setStorageSync('long', res.longitude);
					uni.setStorageSync('lat', res.latitude);
					this.longitude = res.longitude;
					this.latitude = res.latitude;
					this.initForm.lng = res.longitude;
					this.initForm.lat = res.latitude;
					this.getCity();
			    }
			});
		},
		clickitem(idx,val) {
			console.log(val);
			this.initForm.cate_id = val.id;
			this.dian = [];
			this.init();
			// this.initForm.
			
		},
		getFirstBanner() {
			this.api({
				method: "post",
				url: "Index/sowing"
			}).then(res=> {
				// console.log(res);
				this.ban1List = res.data.date;
			})
		},
		getSecondBanner() {
			this.api({
				method: "post",
				url: "Index/newFirendsenjoy"
			}).then(res=> {
				// console.log(res);
				this.ban2List = res.data.data;
			})
		},
		getBusinessClass() {
			this.api({
				method: "post",
				url: "Index/cate_goods_list",
				data: {
					city: this.city,
					area: this.area,
					longitude: this.longitude,
					latitude: this.latitude
				}
			}).then(res=> {
				// console.log(res);
				this.businessList = res.data.data;
				this.initForm.cate_id = parseInt(res.data.data[0].id);
				this.init();
			})
		},
		getTopClass() {
			this.api({
				method: "post",
				url: "Index/shop_cate"
			}).then(res=> {
				// console.log(res);
				this.topList = res.data.date;
			})
		},
		getCity() {
			this.api({
				method: "get",
				url: "Index/change_address",
				data: {
					longitude: this.longitude,
					latitude: this.latitude
				}
			}).then(res=> {
				// console.log(res);
				this.city = res.data.uid.result.ad_info.city;
				this.area = res.data.uid.result.ad_info.district;
				this.locationArea = res.data.uid.result.ad_info.city + "·" + res.data.uid.result.ad_info.district;
				this.getBusinessClass();
			})
		},
		/* 获取 分类下的商家列表 */
		init() {
			// console.log(this.initForm);
			this.api({
				method: "get",
				url: "Index/touch_categoodlist",
				data: this.initForm
			}).then(res=> {
				console.log(res);
				if(res.data.data) {
					res.data.data.forEach(item=> {
						this.dian.push(item);
					})
				} else {
					console.log('莫得东西');
				}
				// res.data.data.forEach(item=> {
				// 	this.dian.push(item);
				// })
			})
		},
		toPage(url) {
			uni.navigateTo({
				url: url
			})
		},
		toHomepage(item) {
			console.log('点击商家，信息为：');
			console.log(item);
			uni.navigateTo({
				url: '../business/homepage?id=' + item.shop_id + "&distance=" + item.distance + "&shop_socre=" + item.shop_socre
			})
		}
	}
}
